
#app {
  font-family: Roboto, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#app header {
    padding: 1rem 1.5rem;
    margin-bottom: 1.5rem;
    background-color: #efefef;
    border-bottom: solid 0.1rem #d4d4d4;
}
#app header h1 {
      margin: 0;
      padding: 0;
      font-size: 2.4rem;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 0.5rem;
}
#app h2 {
    position: relative;
    font-size: 2rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.3rem;
    margin: 1.5rem;
    padding: 0;
}
#app h2:before {
      content: '';
      position: absolute;
      bottom: -1rem;
      left: 50%;
      width: 3rem;
      height: 0.2rem;
      background-color: #898989;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
}
#app h3 {
    font-size: 1.8rem;
    font-weight: 600;
    padding: 0;
    margin: 1.5rem 0;
}
#app form {
    max-width: 40rem;
    margin: auto;
    font-size: 1.4rem;
}
#app form label {
      display: block;
      font-size: 1.4rem;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 0.2rem;
}
#app form input {
      display: block;
      width: 100%;
      margin-bottom: 1.5rem;
      padding: 0.5rem 0.8rem;
      border: solid 0.1rem #d4d4d4;
      font-size: 1.4rem;
      outline: none;
}
#app button {
    padding: 0.5rem 1rem;
    font-size: 1.3rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.2rem;
    background-color: #cdcdcd;
    color: #565656;
    border: solid 0.1rem #b0b0b0;
    cursor: pointer;
    outline: none;
}
#app button:hover {
      background-color: #dedede;
      color: #454545;
      border-color: #c0c0c0;
}
#app button:active {
      -webkit-box-shadow: inset 0 0.1rem 0.5rem rgba(0, 0, 0, 0.2);
              box-shadow: inset 0 0.1rem 0.5rem rgba(0, 0, 0, 0.2);
}
#app .table {
    font-size: 1.4rem;
    -webkit-box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
            box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
}
#app .table td, #app .table th {
      border-color: #cdcdcd;
}
#app .table tbody tr:hover {
      background-color: #e9e9e9;
}
#app .table .consolidated-stat {
      font-size: 1.8rem;
      font-weight: 600;
}
#app .table .percentage-stat {
      color: #898989;
}

































































































































































